.main{
    position: absolute;
    width: 85vw;
    height: 100%;
    right:0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 

    background: url(../course_overview/img/register.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    overflow-x: hidden;
}
/* =========================================================== */
.topbox{
    position: relative;
    display: flex;
    width: 70%;
    height: 60%;

    border-radius: 10px;

    margin-bottom: 50px;
    
    background-color: #f5dbab;
/* 
    box-shadow: 6px 15px 15px rgb(244, 101, 101); */
    box-shadow: 2px 2px 20px rgba(245, 219, 171, 0.4);

}

/* ============================2 */
.top_leftbox{
    position: relative;

    width: 40%;
    height: 100%;

    display: flex;
    justify-content: center;
}

.backbutton{
    position: absolute;
    top :20px;
    left: 20px;
    
    width: 40px;
    height: 40px;

    display: flex;
    justify-content: center;
    align-items: center;

    border: none;
    border-radius: 50%;

    cursor: pointer;
}

.imgbox{
    position: relative;

    width : 250px;
    
    top: 70px;

    border-radius: 6px;
}

.top_leftbox img {
    position: relative;
    top: 15%;
    width: 250px;
    border-radius: 6px;
}

.buttonbox{
    position: absolute;
    width : 100%;
    height : 30%;

    top: 360px;

    display: flex;
    align-items: center;
    justify-content: space-evenly;

    /* background-color: white; */
}

.modify_button{
    border: none;
    border-radius: 5px;

    padding: 10px 30px;

    background-color: #F5F5F5;

    cursor: pointer;

    font-size: 1rem;
    transition: all 0.3s ease;
}

.modify_button:hover {
    transform: translateY(3px);
    box-shadow: 0px 4px 5px #fcbe49;
}

.delete_button{
    border: none;
    border-radius: 5px;

    padding: 10px 30px;

    background-color: #F5F5F5;

    cursor: pointer;

    font-size: 1rem;
    transition: all 0.3s ease;

}

.delete_button:hover {
    transform: translateY(3px);
    box-shadow: 0px 4px 5px #fcbe49;
}

/* ============================2 */

.top_rightbox{
    position: relative;
    top: 30px;
    right: 30px;
    width: 60%;
    height: 350px;

    justify-items: center;
}

.inputboxes{
    position: relative;
    width: 100%;
    height: 100%;

    top: 0px;
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 2열 정렬 */
    gap: 0px; /* 요소 간 간격 */

    margin-bottom: 80px;

    font-size: 1rem;

    justify-items: center;
    align-content: center;

}

/* =========================================================== */

.bottombox{
    position: relative;
    
    width: 70%;
    height: 30vh;

    background-color: #f5dbab;

    border-radius: 10px;

    box-shadow: 2px 2px 20px rgba(245, 219, 171, 0.4);
    
}

.listbox {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 90%;
    height: 26vh;
    overflow-y: auto;
    overflow-x: hidden;
}

.tableHeader {
    display: flex;
    position: relative;
    padding: 20px 0;
    background-color: #f0b549;
    justify-content: space-evenly;

    font-weight: 600;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.tableHeader div {
    width: calc(100% / 5);
    text-align: center;
}

.tableBody {
    table-layout: fixed;
    border-collapse: separate;
    width: 100%;
    text-align: center;
    border-spacing: 0 20px;

    /* background-color: #fff; */
}

.tableBody .tr {
    position: relative;
    width: calc(100% / 5);
    height: 45px;
    box-shadow: 0px 4px 5px rgba(252, 190, 73, 0.6);

    border: 1px solid #8bd2ef;
    background-color: #f5f5f5;
    border-radius: 5px;
}

.tableBody td:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.tableBody td:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

table img {
    width: 50px;
    height: 50px;
    object-fit: cover;
}

table td button {
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: 600;
    text-align: center;
}

.allContentsBox{
    width:100px;
    height: 100px;
    background-color: black;
}