.topmain {
    position: absolute;
    
    /* ============= 레이아웃 ============ */
    width: 85vw;
    height: 100vh;
    overflow-y: auto;
    left: 15vw;
    min-height: 100vh;
    padding-top: 30vh;
    box-sizing: border-box;

    /* ================================= */
    background: url(./img/register.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
    overflow-x: hidden;
}

.main {
    /* ============= 레이아웃 ============ */
    /* ================================= */
    
    width: 100%;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    gap: 100px;
    justify-content: center;
    align-items: center;

    z-index: 20;
}

/* head 및 topmenu */
.headline {
    position: absolute;

    top : 50px;
    left: 110px;
    color: #f5f5f5;
    filter: drop-shadow(0 4px 4px rgba(255, 255, 255, 0.4));
    font-size: 36px;
    font-weight: bold;
}

/* 과정등록 */

.registerbutton {
    position: fixed;

    top: 150px;
    left: 105px;

    width: 120px;
    
    border-radius: 7px;

}
.register {
    position: relative;
    width: 130px;
    left: 0;
    padding: 11px 20px;
    height: 45px;

    background-color: #f3bb2e;
    border: none;
    border-radius: 7px;
    font-size: 1rem;
    cursor: pointer;

    transition: all 0.3s ease;
}

.register:hover {
    transform: translateY(3px); /* hover 시 이동 */
    box-shadow: 4px 4px 10px #edc12e;
}

.search {
    position: absolute;
    display: flex;

    left: 110px;
    top: 150px;

}

.rightBtn {
    display: flex;
    position: absolute;
    top: 150px;
    right: 3vw;
}

.drop1, .drop2 {
    position: relative;
    margin-right: 20px;
    padding: 10px;
    height: 45px;
    font-size: 1rem;

    border: 1px solid  #edc12e;
    background-color:#F5F5F5;

    border-radius: 5px;
}

.search_bar {
    position: relative;

    width: 300px;
    height: 45px;

    font-size: 1rem;
    padding: 10px;
    box-sizing: border-box;
    background-color: #F5F5F5;
    
    border: 1px solid  #edc12e;
    border-radius: 5px;
}

.icon {
    position: absolute;
    top: 50%;
    right: 2%;

    transform: translateY(-50%);
    background-color: transparent;
    border: none;
    cursor: pointer;

    color: #9b9898;;
    font-size: 1rem;
}

.allContentsBox {
    position: relative;

    height: 250px;

}

.allContentsBox:hover {
    position: relative;

    transform: translateY(5px);
    transition: transform 0.3s ease; 
}


.contentsBox {
    position: relative;

    display: flex;
    flex-direction: column;

    width: 250px;
    height: 100%;
    border-radius: 5px;

    background-color: #f8efc0;
    
    z-index: 10;

    cursor: pointer;
}


.category {
    width: 250px;
    height: 20px;
    padding-left: 20px;
    box-sizing: border-box;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

/* ////////////////////////////////////////////////// */

.contentsBox .imgbox {
    position: relative;
    width: 200px;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px;
}

.contentsBox img {
    border-radius: 5px;
    width: 200px;
}

.state {
    position: absolute;

    right: 10px;
    bottom: 10px;

    width: 60px;
    text-align: center;
    box-sizing: border-box;
    
    background-color: #BF0700;
    border: 1px solid #f9c771;
    color: #F5F5F5;
    border: 1px solid #F5F5F5;
    border-radius: 5px;

    box-shadow: none !important; 
}

.contentsbody {
    position: relative;
    width: 250px;
    height: 250px;
}

.courseName {
    width: 200px;
    margin: 5px 0;
    padding-left: 20px;
    overflow: hidden;
    font-size: 0.8rem;
    text-align: left;

}

.date {
    text-align: left;
    padding-left: 20px;
    font-size: 0.8rem;
    margin-bottom: 5px;
}

.contentsfooter {
    position: absolute;

    display: flex;
    align-items: center;

    padding: 5px 10px;

    width: 250px;
    height: 30px;
    bottom: 0;

    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    box-sizing: border-box;
    background-color: #f7ba57;
    box-shadow: none !important; 
}

.instructorname {
    position: relative;
    padding-left: 5px;
    width: 50%;

    box-shadow: none !important; 
}

.instructor {
    
    margin-left: 5px;
    width: 80px;

    box-shadow: none !important; 
}

.capacity {
    position: relative;
    margin-left: 20px;
    margin-right: 4px;
    top: 25%;
    width: 50px;
    height: 30px;

    box-shadow: none !important; 
}
